@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700;900&family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,400;1,500;1,700;1,900&display=swap');
html, html::before, html::after, body, body::before, body::after {
  font-size: 10px;
  line-height: 1.5;
  font-family: 'Roboto', 'Noto Sans TC', sans-serif;
  font-weight: 400;
}
.main > * > *, .footer > *, .footer > * > * {
  width: 1110px;
  margin-left: auto;
  margin-right: auto;
}
@media (max-width:768px) {
  .main > * > *, .footer > *, .footer > * > * {
    width: 690px;
  }
}
@media (max-width:375px) {
  .top0-header, .main, .footer {
    padding-left: 15px;
    padding-right: 15px;
  }
  .top0-header, .main > *, .footer > * {
    width: 375px;
  }
  .main > * > *, .footer > * > * {
    width: 345px;
  }
}
@media (max-width:374px) {
  .top0-header, .main > *, .footer > * {
    width: 345px;
  }
}
img {
  max-width: 100%;
  height: auto;
  display: block;
}
a {
  color: #fff;
  text-decoration: none;
  display: block;
}
a:hover {
  text-decoration: underline;
}
h2 {
  font-size: 4.8rem;
  text-align: center;
  margin-bottom: 40px;
  color: #650300;
  font-weight: 700;
}
@media (max-width:768px) {
  h2 {
    font-size: 3.2rem;
    margin-bottom: 32px;
  }
}
@media (max-width:375px) {
  h2 {
    font-size: 2.4rem;
    margin-bottom: 20px;
  }
}
.top0-header .title {
  color: #fff;
  background-color: #AA0601;
  padding-left: 88px;
  padding-right: 88px;
  height: 108px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width:768px) {
  .top0-header .title {
    padding-left: 39px;
    padding-right: 39px;
  }
}
@media (max-width:375px) {
  .top0-header .title {
    padding-left: 0;
    padding-right: 0;
    display: block;
    height: 100%;
  }
  .top0-header .title * {
    display: block;
  }
}
.top0-header .title li {
  display: inline-block;
  font-size: 2rem;
}
.top0-header .title li:not(:first-of-type) {
  margin-left: 64px;
}
@media (max-width:768px) {
  .top0-header .title li:not(:first-of-type) {
    margin-left: 40px;
  }
}
@media (max-width:375px) {
  .top0-header .title .logo {
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #fff;
  }
  .top0-header .title .logo img {
    width: 48px;
    height: 29px;
    margin-left: auto;
    margin-right: auto;
  }
  .top0-header .title li:not(:first-of-type) {
    margin-left: 0;
  }
  .top0-header .title li {
    display: inline-block;
    box-sizing: border-box;
    width: 50%;
    border: 1px solid #fff;
    border-top: none;
    text-align: center;
    padding-top: 8px;
    padding-bottom: 8px;
  }
  .top0-header .title li a {
    line-height: 2.9rem;
  }
  .top0-header .title li:nth-child(odd) {
    border-right: none;
  }
}
.top0-header .banner {
  margin: 0 auto;
  padding-right: 405px;
  background: url("/assets/images/banner.png") no-repeat center center;
  background-size: cover;
  height: 640px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: #650300;
}
.top0-header .banner h1 {
  text-align: right;
  font-size: 3.6rem;
}
.top0-header .banner h1 span {
  font-size: 6.4rem;
  line-height: 9.6rem;
  font-weight: 700;
}
@media (max-width:768px) {
  .top0-header .banner {
    padding-right: 38.5px;
  }
  .top0-header .banner h1 {
    font-size: 2rem;
  }
  .top0-header .banner h1 span {
    font-size: 4.8rem;
    line-height: 7.2rem;
  }
}
@media (max-width:375px) {
  .top0-header .banner {
    background: url("/assets/images/banner-sm.png") no-repeat center center;
    background-size: cover;
    height: 394px;
    padding-right: 15px;
  }
  .top0-header .banner h1 {
    font-size: 1.6rem;
    line-height: 2.2rem;
  }
  .top0-header .banner h1 span {
    font-size: 3.2rem;
    line-height: 4.5rem;
  }
}

.main .advantage {
  padding-top: 80px;
  padding-bottom: 60px;
  background-color: #AA06010D;
}
@media (max-width:768px) {
  .main .advantage {
