}
@media (max-width:768px) {
  .main .advantage ul {
    width: 615px;
  }
}
@media (max-width:375px) {
  .main .advantage ul {
    width: 255px;
  }
}
.main .advantage li {
  width: 255px;
  display: block;
}
@media (max-width:768px) {
  .main .advantage h2 {
    margin-bottom: 0;
  }
  .main .advantage li {
    margin-top: 32px;
  }
}
.main .advantage .icon {
