  }
}
.main .classic .product-name {
  width: 475px;
  margin: auto 0;
  display: flex;
  font-style: italic;
}
.main .classic li:nth-child(even) .product-name {
  justify-content: flex-end;
}
.main .classic .product-name > a {
  display: inline-block;
}
.main .classic .product-name span a {
  display: inline;
