  }
}
@media (max-width:375px) {
  .main .classic .product-name {
    width: 345px;
    height: 40px;
    display: flex;
    align-content: center;
    justify-content: center !important;
    align-items: center;
    position: absolute;
    top: 35.07%;
  }
}
.main .classic li {
  display: flex;
  font-size: 4.8rem;
  color: #AA0601;
}
@media (max-width:768px) {
