    position: relative;
  }
}
.main .classic li > img {
  width: 635px;
  height: 320px;
}
@media (max-width:768px) {
  .main .classic li > img {
    width: 390px;
    height: 168px;
  }
}
@media (max-width:375px) {
  .main .classic li > img {
    width: 345px;
    height: 134px;
  }
}
.main .classic li:not(:last-of-type) {
  margin-bottom: 32px;
}
@media (max-width:375px) {
  .main .classic li:not(:last-of-type) {
    margin-bottom: 8px;
  }
}
.main .classic li .product-name-icon {
  display: inline-block;
  vertical-align: middle;
  margin-left: 32px;
  margin-right: 8px;
}
.main .classic li:nth-child(even) .product-name-icon {
  margin-left: 0;
}
@media (max-width:768px) {
  .main .classic li .product-name-icon {
    width: 40px;
    height: 40px;
  }
}
@media (max-width:375px) {
  .main .classic li .product-name-icon {
    margin-left: 0;
  }
}
.main .classic li .product-name:nth-child(odd) {
  text-align: right;
  padding-right: 30px;
}
@media (max-width:768px) {
  .main .classic li:nth-child(even) {
    display: flex;
    flex-direction: row-reverse;
  }
  .main .classic li:nth-child(even) .product-name {
    padding-right: 0;
  }
  .main .classic li:nth-child(even) .product-name img {
    margin-left: 32px;
  }
}
@media (max-width:375px) {
  .main .classic li:nth-child(even) {
    display: block !important;
  }
  .main .classic li:nth-child(even) .product-name img {
    margin-left: 0;
  }
}

