.footer {
  background-color: #AA0601;
  color: #fff;
  padding-top: 32px;
  padding-bottom: 39px;
  font-size: 1.6rem;
}
@media (max-width:768px) {
  .footer > * {
    width: 690px;
  }
}
@media (max-width:375px) {
  .footer {
    padding-top: 24px;
    padding-bottom: 24px;
    padding-left: 16px;
    padding-right: 16px;
    position: relative;
    box-sizing: border-box;
    font-size: 1.4rem;
  }
  .footer > * {
    width: 345px;
  }
  .footer .footer-top nav {
    display: none;
  }
  .footer .footer-middle {
    position: absolute;
    top: 24px;
  }
}
@media (max-width:375px) {
  .footer > * {
    width: 100%;
  }
}
.footer .footer-top, .footer .footer-top ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer .footer-top nav {
  width: 464px;
  margin-left: 0;
}
.footer .footer-top > ul {
  width: 208px;
  margin-right: 0;
}
@media (max-width:768px) {
  .footer .footer-top nav {
    width: 432px;
  }
  .footer .footer-top > ul {
    width: 120px;
  }
  .footer .footer-top > ul img {
    width: 24px;
    height: 24px;
  }
}
.footer .footer-middle {
  margin-top: 24px;
}
@media (max-width:375px) {
  .footer .footer-middle {
    margin-top: 0;
  }
  .footer .footer-middle ul {
    margin-left: 0;
  }
}
.footer .footer-middle li:not(:last-of-type) {
  margin-bottom: 16px;
}
@media (max-width:768px) {
  .footer .footer-middle li:not(:last-of-type) {
    margin-bottom: 12px;
  }
}
.footer .footer-middle li * {
  display: inline-block;
  vertical-align: middle;
}
.footer .footer-middle li p {
  font-size: 3.6rem;
  margin-left: 16px;
}
@media (max-width:768px) {
  .footer .footer-middle li p {
    font-size: 2rem;
    margin-left: 8px;
  }
  .footer .footer-middle li img {
    width: 18px;
    height: 18px;
  }
}
@media (max-width:375px) {
  .footer .footer-middle li p {
    font-size: 1.6rem;
    margin-left: 6px;
  }
}
.footer .custom-hr {
  border: none;
  border-top: 1px solid #fff;
  margin-top: 24px;
  margin-bottom: 32px;
}
@media (max-width:768px) {
  .footer .custom-hr {
    margin-top: 20px;
    margin-bottom: 24px;
  }
}
@media (max-width:375px) {
  .footer .custom-hr {
    margin-top: 49px;
    margin-bottom: 16px;
  }
}
.footer .footer-bottom ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width:375px) {
  .footer .footer-bottom ul {
    display: block;
  }
}
.footer .footer-bottom ul li small {
  font-size: 1.4rem;
}
.footer .footer-bottom ul li {
  display: block;
}
@media (max-width:375px) {
  .footer .footer-bottom ul li:not(:last-of-type) {
    margin-bottom: 8px;
  }
}
.footer .footer-bottom ul li:first-of-type {
  flex-grow: 1;
}
.footer .footer-bottom ul li:last-of-type {
  margin-left: 48px;
}
@media (max-width:768px) {
  .footer .footer-bottom ul li:last-of-type {
    margin-left: 24px;
  }
}
@media (max-width:375px) {
  .footer .footer-bottom ul li:last-of-type {
    margin-left: 0;
  }
}
